import { TransactionInstruction, Keypair, PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import {
  NetworkTraderFeePayerArgs,
  TraderAccountsArgs,
  TraderPositionAccountArgs,
  UpdatePositionArgs,
} from '../borsh';
import {
  createNetworkTraderFeePayerInstruction,
  createTraderAccountInstruction,
  createPlacePositionInstruction,
  createTraderPositionAccountInstruction,
  createAssociatedTokenAccountInstruction,
} from '../instructions';
import { PositionSideEnum, WalletSigner } from '../types';
import {
  findAssociatedTokenAccountPubkey,
  findNetworkTraderAccountPubkey,
  findMarketTraderAccountPubkey,
  findNetworkTraderFeePayerPubkey,
  findTraderPositionAccountPubkey,
  findEntryAccountPubkey,
  findHoneypotPubkey,
  decodeNetwork,
  decodeParimutuel,
  sendTransaction,
} from '../utils';

export async function sendPlacePositionTransaction(
  this: ParimutuelWeb3,
  userWallet: WalletSigner | Keypair,
  parimutuelPubkey: PublicKey,
  size: number,
  side: PositionSideEnum,
  seq: number,
): Promise<string> {
  if (!userWallet.publicKey) throw new Error('user wallet not connected');

  const instructions: TransactionInstruction[] = [];
  const signers: Keypair[] = [];
  const { PARIMUTUEL_PROGRAM_ID, NETWORK } = this.config;
  const userWalletPubkey = userWallet.publicKey;

  // eslint-disable-next-line no-prototype-builtins
  if (userWallet?.hasOwnProperty('_keypair')) {
    signers.push(userWallet as Keypair);
  }

  const networkPubkey = NETWORK;

  const [networkAccount, parimutuelAccount] =
    await this.connection.getMultipleAccountsInfo([
      networkPubkey,
      parimutuelPubkey,
    ]);

  if (!networkAccount) {
    throw new Error('no network account'); // TODO: centralize error
  }

  if (!parimutuelAccount) {
    throw new Error('no parimutuel account');
  }

  const decodedParimutuelAccount = decodeParimutuel(
    Buffer.from(parimutuelAccount.data)
  );
  const marketPubkey = new PublicKey(decodedParimutuelAccount.marketKey);

  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));
  const protocolMintPubkey = new PublicKey(decodedNetworkAccount.protocolToken);
  const settlementMintPubkey = new PublicKey(
    decodedNetworkAccount.settlementToken
  );

  const protocolTokenAccountPubkey = await findAssociatedTokenAccountPubkey(
    userWalletPubkey,
    protocolMintPubkey
  );

  const settlementTokenAccountPubkey = await findAssociatedTokenAccountPubkey(
    userWalletPubkey,
    settlementMintPubkey
  );

  const [protocolTokenAccount, settlementTokenAccount] =
    await this.connection.getMultipleAccountsInfo([
      protocolTokenAccountPubkey,
      settlementTokenAccountPubkey,
    ]);

  if (!protocolTokenAccount) {
    const instruction = createAssociatedTokenAccountInstruction(
      protocolTokenAccountPubkey,
      userWalletPubkey,
      userWalletPubkey,
      protocolMintPubkey
    );
    instructions.push(instruction);
  }

  if (!settlementTokenAccount) {
    const instruction = createAssociatedTokenAccountInstruction(
      settlementTokenAccountPubkey,
      userWalletPubkey,
      userWalletPubkey,
      settlementMintPubkey
    );
    instructions.push(instruction);
  }

  const networkTraderAccountPubkey = await findNetworkTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    userWalletPubkey,
    networkPubkey
  );

  const marketTraderAccountPubkey = await findMarketTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    userWalletPubkey,
    marketPubkey
  );

  const networkTraderFeePayerPubkey = await findNetworkTraderFeePayerPubkey(
    PARIMUTUEL_PROGRAM_ID,
    networkTraderAccountPubkey
  );

  const traderPositionAccountPubkey = await findTraderPositionAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    userWalletPubkey,
    parimutuelPubkey
  );

  const entryAccountPubkey = await findEntryAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    traderPositionAccountPubkey,
    seq
  );

  const honeypotPubkey = await findHoneypotPubkey(
    PARIMUTUEL_PROGRAM_ID,
    parimutuelPubkey
  );

  const traderAccountInstruction = await createTraderAccountInstruction(
    PARIMUTUEL_PROGRAM_ID,
    new TraderAccountsArgs(),
    networkPubkey,
    marketPubkey,
    userWalletPubkey,
    protocolTokenAccountPubkey,
    settlementTokenAccountPubkey,
    networkTraderAccountPubkey,
    marketTraderAccountPubkey
  );

  const feePayerInstruction = await createNetworkTraderFeePayerInstruction(
    PARIMUTUEL_PROGRAM_ID,
    new NetworkTraderFeePayerArgs(),
    networkPubkey,
    userWalletPubkey,
    protocolMintPubkey,
    networkTraderAccountPubkey,
    networkTraderFeePayerPubkey
  );

  const traderPositionAccountInstruction =
    await createTraderPositionAccountInstruction(
      PARIMUTUEL_PROGRAM_ID,
      new TraderPositionAccountArgs(),
      networkPubkey,
      marketPubkey,
      userWalletPubkey,
      traderPositionAccountPubkey,
      parimutuelPubkey
    );

  const updatePositionInstruction = await createPlacePositionInstruction(
    PARIMUTUEL_PROGRAM_ID,
    new UpdatePositionArgs({ size, side, seq }),
    networkPubkey,
    marketPubkey,
    userWalletPubkey,
    settlementTokenAccountPubkey,
    traderPositionAccountPubkey,
    entryAccountPubkey,
    honeypotPubkey,
    parimutuelPubkey
  );

  const response = await sendTransaction(
    this.connection,
    userWallet as WalletSigner,
    [
      ...instructions,
      traderAccountInstruction,
      feePayerInstruction,
      traderPositionAccountInstruction,
      updatePositionInstruction,
    ],
    signers,
    'max',
    signers.length > 0
  );

  if (!response) {
    throw new Error('failed to update position');
  } else {
    return response.transactionId;
  }
}
